.usersContainer {
    height: 90%;
    margin: auto;
    margin-left: 10%;

    display: flex;
    flex-direction: column;
    gap: 3%;
  }
  
  .tileContainer {
    height: 20%;
    align-items: center;
    background-color: #D9D9D9;
    border-radius: 25px;
  
    display: grid;
    grid-template-columns: 1fr 2fr 0.5fr;
  }
  .tileContainer img {
    width: 60%;
    border-radius: 50px;
    margin: auto;
  }
  .tileContainer-text{
  
    width: 100%;
    height: 100%;
    
    display: flex;
    flex-direction: column;
  }
  .tileContainer-text h2{
    text-transform: uppercase;
    height: 50%;
    font-size: 1.5vw;

    display: flex;
    justify-content: center;
    align-items: center;
  }
  .tileContainer-text p{
    text-transform: uppercase;
    height: 50%;
    font-size: 1vw;
    text-align: center;
  }

.buttonSelect{
    width: 80%;
    margin: auto;
    height: 30px;
    font-size: 13px;
    background-color: #0D1B2A;
    border-radius: 30px;
    border: none;
    color: white;
    cursor: pointer;
    font-size: 0.6vw;
}  
.buttonAdd{
    position: relative;
    background-color: white;
    color: #0D1B2A;
    height: 5%;

    width: 30%;
    left: 70%;
    display: flex;
    border: none;
    border-radius: 45px;
    font-size: 1vw;
    font-weight: 500;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.newUser input{
    height: 40%;
    width: 80%;
   margin: auto;
   background-color: transparent;
   border-radius: 30px;
   border: solid 2px #0D1B2A;
   text-align: center;
   font-size: 1.5vw;
}
.newUser button{
    background-color: #0D1B2A;
    color: white;
    height: 20%;
    width: 80%;
    border: none;
    border-radius: 20px;
    margin: auto;
    cursor: pointer;
}
.newUser img{cursor: pointer;}